import React, { Component } from 'react';
//import ReactDOM from "react-dom";
//import logo from './logo.svg';
import './App.css';
import Gallery from "./Gallery";
import InputCtrl from "./InputCtrl";
import ConvCtrl from "./ConvCtrl";
import ModelCtrl from "./ModelCtrl";
import {getImageUrls_color, getImageUrls_gray, getImageUrls_binary} from './ImageUrls';
import * as tf from '@tensorflow/tfjs';

class App extends Component {
  constructor(props, context) {//Class constructor
    super(props, context);
    
    //Class state variables which will trigger rerendering when changed
    this.state = {
      crayfishUrls: [],
      elephantUrls: [],
      flamingoUrls: [],
      hedgehogUrls: [],
      kangarooUrls: [],
      leopardUrls: [],
      galleryUrls: [],
      galleryLabels: [],
      galleryFormat: 'gray',
      galleryIsHidden: true,
    }
    
    this.toggleGallery = this.toggleGallery.bind(this);
    this.saveFeedbackFromInputCtrl = this.saveFeedbackFromInputCtrl.bind(this);
  }
  
  componentDidMount() {
    //Sort array in random order
    //var arr = [1, 2, 3, 4, 5, 7, 8, 9];
    //var arr1 = arr.sort(function (a,b) { return Math.floor(Math.random() * arr.length)});
    //console.log(arr1);
    console.log(tf.util.createShuffledIndices(70));
  }
  
  saveFeedbackFromInputCtrl(format, categories, ratio, batchSize) {
    console.log(format);
    console.log(categories);
    console.log(ratio);
    console.log(batchSize);
  }
  
  saveFeedbackFromConvCtrl(){
    console.log('saveFeedbackFromConvCtrl');
  }
  
  toggleGallery(label, format) {
    if((this.state.galleryLabels === label) && (this.state.galleryFormat === format)){
      this.setState({
        galleryIsHidden: true
      });
    }
    else {
      var urls = [];
      switch(format) {
        case 'color':
          urls = getImageUrls_color(label);
          break;
        case 'gray':
          urls = getImageUrls_gray(label);
          break;
        case 'binary':
          urls = getImageUrls_binary(label);
          break;
        default:
          urls = getImageUrls_color(label);
      }
      
      this.setState({
        galleryIsHidden: false
      });
      
      this.setState({
        galleryFormat: format
      });
      
      this.setState({galleryUrls: urls}, function () {
        //console.log();
      });
      this.setState({galleryLabels: label}, function () {
        //console.log();
      });
    }
  }

  render() {
    //console.log('rendering');
    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title">My image object recognition machine</h1>
        </header>
        <p className="App-intro">
          To get started, configure the network below and press the play button when ready.
        </p>
        <div className="controlPanel">
          <InputCtrl toggleGallery={this.toggleGallery}
                     saveSettings={this.saveFeedbackFromInputCtrl}/>
          <ConvCtrl saveSettings={this.saveFeedbackFromConvCtrl}/>
          <ModelCtrl saveSettings={this.saveFeedbackFromConvCtrl}/>
        </div>
        {!this.state.galleryIsHidden && <Gallery imageUrls={this.state.galleryUrls}
                                                 imageLabels={this.state.galleryLabels}/>}
      </div>
    );
  }
}

export default App;



