import React from 'react';
import Checkbox from './Checkbox'; //http://react.tips/checkboxes-in-react/

class ConvCtrl extends React.Component {
  constructor () {
    super()
    this.state = {
      nrOfLayers: 1,
      filterSize: [],
      stride: [],
      padding: [],
      activationFunction: [],
      poolLayer: []
    }

    this.saveClickedLabelFeedback = this.saveClickedLabelFeedback.bind(this);
    this.feedBackSettings = this.feedBackSettings.bind(this);
  }
  
  saveClickedLabelFeedback(label) {
    //Do nothing
  }
  
  feedBackSettings() {
    this.props.saveSettings();
  }

  render() {
    var boldFont = {
      fontWeight: 'bold'
    };
    return(
      <div className="controlPanelLanel">
        <h3>Convolutional layer(s)</h3>
        <hr />
        <p style={boldFont}>Text</p>
        <Checkbox label='Pool layer'
                  handleCheckboxChange={this.saveCheckboxFeedback}
                  clickedLabel={this.saveClickedLabelFeedback}/>
        <button onClick={this.feedBackSettings.bind(this)} >
          Save
        </button>
      </div>
    );
  }
}
 
export default ConvCtrl;


