import React from 'react';

class ModelCtrl extends React.Component {
  constructor () {
    super()
    this.state = {
      modelOk: false
    }

    this.feedBackSettings = this.feedBackSettings.bind(this);
  }
  
  feedBackSettings() {
    this.props.saveSettings();
  }

  render() {
    var boldFont = {
      fontWeight: 'bold'
    };
    return(
      <div className="controlPanelLanel">
        <h3>Create and Run</h3>
        <hr />
        <p style={boldFont}>Text</p>
        <button onClick={this.feedBackSettings.bind(this)} >
          Create Machine
        </button>
        <button onClick={this.feedBackSettings.bind(this)} >
          Run Machine
        </button>
      </div>
    );
  }
}
 
export default ModelCtrl;


